.account,
.account-dark {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid black;
    background-color: #fff;
    width: 80%;
    margin: 0 auto;
    flex-direction: column;
    padding: 1.5rem;
    box-sizing: border-box;
    text-align: left;
    margin-bottom: 2rem;
}

.account-dark {
    border: none;
    border-radius: 5px;
    background: #3d3d3d;
    color: #fff;
    flex-direction: row;
    padding: 1.5rem;
}
.account-amount {
    margin: 0;
    font-size: 2.5rem;
    font-weight: bold;
}
  
.account-amount-description {
    margin: 0;
}
  
.account-title {
    margin: 0;
    padding: 0;
    font-size: 1rem;
    font-weight: normal;
}
  
.account-content-wrapper {
    width: 100%;
    flex: 1;
}

.transaction-button {
    display: block;
    width: 100%;
    padding: 8px;
    font-size: 1.1rem;
    font-weight: bold;
    margin-top: 1rem;
    border-color: #00bc77;
    background-color: #00bc77;
    color: #fff;
}
  
@media screen and (min-width: 720px) {
    .account {
      flex-direction: row;
    }
  
    .account-content-wrapper.cta {
      flex: 0;
    }
  
    .transaction-button {
      width: 200px;
    }
}
@media screen and (max-width: 400px) {
    .account-title {
    font-size: 0.8rem;
    }
   .account-amount {
    font-size: 2rem;
    }
}